<template>
  <div class="center">
    <el-card class="cart">
      <el-breadcrumb separator=">" class="bread">
        <el-breadcrumb-item :to="{ path: '/about' }"
          ><i class="el-icon-location-outline"></i>首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>月子中心</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="classify-box">
        <classify :title="'所在省份'" :content="content"></classify>
        <classify
          :title="'价格区间'"
          :content="['5千-1万', '1万-2万', '2-5万', '5万-10万', '10万以上']"
        ></classify>
        <classify
          :title="'类型'"
          :content="['酒店式', '公寓式', '别墅式', '医院式', '独栋式']"
        ></classify>
      </div>
      <div class="icon-box" @click="more">
        <i class="el-icon-caret-bottom icon" v-if="contentCount"> </i>
        <i class="el-icon-caret-top" v-else></i>
      </div>
    </el-card>
    <el-card class="problem"> </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: [
        "北京",
        "上海",
        "天津",
        "河北",
        "内蒙古",
      ],
      contentCount: 0
    };
  },
  methods: {
    more(){
      if(this.contentCount === 0){
         this.content.push('河北','山西','辽宁','吉林','黑龙江','江苏','浙江','安徽',
        '福建','江西','山东','河南','湖北','湖南','广东','海南','四川','贵州','云南','陕西','甘肃','青海','台湾');
        this.contentCount = 1;
      }else{
        this.content = ["北京","上海","天津","河北","内蒙古"]
        this.contentCount = 0;
      }
     
    },
  },
};
</script>

<style scoped>
.center {
  position: relative;
  width: 1400px;
  margin: auto;
  display: flex;
  justify-content: space-between;
}
.bread {
  font-size: 16px;
}

.cart {
  width: 1000px;
}
.problem {
  width: 300px;
}
.classify-box {
  margin-top: 20px;
  border-bottom: 1px solid #ccc;
}
.icon-box {
  position: absolute;
  left: 940px;
  top: 70px;
  color: #f40;
  font-size: 30px;
  cursor: pointer;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.icon-box:hover {
  color: #dc3023;
}
</style>


